import React, { Component } from 'react';
import { observer, inject } from 'mobx-react';
import * as ethers from 'ethers/index.js';
import Config from '../../../contract/config.json';
import { Event } from '../../../contract/index';
import dayjs from 'dayjs';

@inject('ethereumStore')
@observer
export default class Guess extends Component {
    constructor(props) {
        super(props)
        this.state = {
            openHistory: false,
            history: [],
            getBettingPrice: '',
            getBettingStatus: []
        }
        this.provider = this.props.ethereumStore.provider;
        this.contract = new ethers.Contract(Config["1000Guess"][3].contractAddress, Config["1000Guess"].abi, this.provider)
    }
    componentWillMount = () => {
        this.getInfo()
        this.interval = setInterval(() => {
            this.getInfo();
            this.state.openHistory && this.getHistory()
        }, 4000)
    }
    componentWillUnmount = () => {
        clearInterval(this.interval)
    }

    getInfo = async () => {
        let getBettingStatus = this.contract.getBettingStatus();
        let getBalance = await this.contract.getBalance();
        let getDeveloperAddress = await this.contract.getDeveloperAddress();
        let getMaxContenders = this.contract.getMaxContenders();
        let getBettingPrice = this.contract.getBettingPrice();
        let getDeveloperFee = this.contract.getDeveloperFee();
        let getLotteryMoney = this.contract.getLotteryMoney();
        let state = this.contract.state();

        [getBettingStatus, getBalance, getDeveloperAddress, getMaxContenders, getBettingPrice, getDeveloperFee, getLotteryMoney, state] = JSON.parse(JSON.stringify(await Promise.all([getBettingStatus, getBalance, getDeveloperAddress, getMaxContenders, getBettingPrice, getDeveloperFee, getLotteryMoney, state])))
        getBalance = ethers.utils.formatEther(getBalance)
        getDeveloperFee = ethers.utils.formatEther(getDeveloperFee)
        getBettingPrice = ethers.utils.formatEther(getBettingPrice)
        getLotteryMoney = ethers.utils.formatEther(getLotteryMoney)
        for (let i = 2; i <= 4; i++) {
            getBettingStatus[i] = ethers.utils.formatEther(getBettingStatus[i])
        }

        this.setState({
            getBettingStatus, getBalance, getDeveloperAddress, getMaxContenders, getMaxContenders,
            getBettingPrice, getDeveloperFee, getLotteryMoney, state
        })
    }

    addguessBtn = async () => {
        let signer = this.contract.connect(this.props.ethereumStore.wallet.connect(this.provider));
        let tx = await signer.addguess(this.guess, { value: ethers.utils.parseEther(this.state.getBettingPrice) });
        alert('txid: ' + tx.hash);
        await tx.wait();
        this.getInfo();
    }

    finishBtn = async () => {
        let signer = this.contract.connect(this.props.ethereumStore.wallet.connect(this.provider));
        let tx = await signer.finish();
        alert('txid: ' + tx.hash);
        await tx.wait();
        this.getInfo();
    }

    setBettingConditionBtn = async () => {
        let signer = this.contract.connect(this.props.ethereumStore.wallet.connect(this.provider));
        let tx = await signer.setBettingCondition(this.contenders, ethers.utils.parseEther(this.bettingprice));
        alert('txid: ' + tx.hash);
        await tx.wait();
        this.getInfo();
    }

    getHistory = async () => {
        let SentPrizeToWinner = Event.bind(this.provider)(this.contract, 'SentPrizeToWinner')
        let SentDeveloperFee = Event.bind(this.provider)(this.contract, 'SentDeveloperFee')
        SentPrizeToWinner = await SentPrizeToWinner;
        SentDeveloperFee = await SentDeveloperFee;
        SentPrizeToWinner = SentPrizeToWinner.map(v => {
            let [winner, money, guess, gameindex, lotterynumber, timestamp] = v
            return { winner, money: ethers.utils.formatEther(money), guess, gameindex, lotterynumber, timestamp: dayjs(timestamp * 1000).format('YYYY年MM月DD日 HH:mm:ss'), transactionHash: v.transactionHash };
        })
        SentDeveloperFee = SentDeveloperFee.map(v => {
            let [amount, balance] = v

            return { amount: ethers.utils.formatEther(amount), balance: ethers.utils.formatEther(balance), transactionHash: v.transactionHash };
        })
        let rows = SentDeveloperFee.map(v => {
            let winners = SentPrizeToWinner.filter(x => {
                return x.transactionHash == v.transactionHash
            })
            return {
                ...v,
                winners
            }
        })

        rows = rows.reverse()
        this.setState({ history: JSON.parse(JSON.stringify(rows)) });
    }

    getHistoryReact() {
        let rows = this.state.history.map((v, i) => {
            let winners = v.winners.map((winner, i) => {
                return [
                    <td key={winner.guess}>{winner.guess}</td>,
                    <td key={winner.lotterynumber}>{winner.lotterynumber}</td>,
                    <td key={winner.money}>{winner.money}</td>,
                    <td key={winner.timestamp}>{winner.timestamp}</td>,
                    <td key={winner.winner}>{winner.winner}</td>
                ]
            })
            let winner = winners.shift()
            winners = winners.map(winner => <tr key={winner.winner}>{winner}</tr>);
            return (
                [<tr key={i}>
                    <td rowSpan={v.winners.length || 1}>{i + 1}</td>
                    <td rowSpan={v.winners.length || 1}>{v.amount}</td>
                    <td rowSpan={v.winners.length || 1}>{v.balance}</td>
                    {v.winners.length ? winner :
                        <td colSpan={v.winners.length ? 1 : 5} ></td>
                    }
                </tr>, winners]
            )
        })
        return (
            <table className="table table-bordered">
                <thead>
                    <tr>
                        <th>#</th>
                        <th>莊家手續費(ether)</th>
                        <th>餘額(ether)</th>
                        <th>猜測值</th>
                        <th>開獎號碼</th>
                        <th>贏家所獲得彩金(ether)</th>
                        <th>開牌日期</th>
                        <th>贏家</th>
                    </tr>
                </thead>
                <tbody>
                    {rows}
                </tbody>
            </table>
        )
    }

    render() {

        let getBettingStatus = (
            <table>
                <tbody>
                    <tr><th>合約狀態</th><td>{this.state.getBettingStatus[0]}</td></tr>
                    <tr><th>累積猜測次數</th><td>{this.state.getBettingStatus[1]}</td></tr>
                    <tr><th>累積樂透淨額</th><td>{this.state.getBettingStatus[2]}</td></tr>
                    <tr><th>合約餘額</th><td>{this.state.getBettingStatus[3]}</td></tr>
                    <tr><th>每注金額</th><td>{this.state.getBettingStatus[4]}</td></tr>
                </tbody>
            </table>
        )



        return (
            <div className="col-xs-12">
                <table className="table table-striped">
                    <tbody>
                        <tr>
                            <td>狀態</td>
                            <td>{this.state.state}</td>
                            <td>合約餘額</td>
                            <td>{this.state.getBalance}</td>
                        </tr>
                        <tr>
                            <td>最大競爭人數</td>
                            <td>{this.state.getMaxContenders}</td>
                            <td>累積樂透淨額</td>
                            <td>{this.state.getLotteryMoney}</td>
                        </tr>
                        <tr>
                            <td>莊家抽頭</td>
                            <td>{this.state.getDeveloperFee}</td>
                            <td>投注金額</td>
                            <td>{this.state.getBettingPrice}</td>
                        </tr>
                        <tr>
                            <td>開發者地址</td>
                            <td>{this.state.getDeveloperAddress}</td>
                            <td>投注狀態</td>
                            <td>{getBettingStatus}</td>
                        </tr>
                    </tbody>
                </table>
                <div className="box box-primary">
                    <div className="box-header with-border">
                        <h3 className="box-title">下注</h3>
                    </div>
                    <div className="box-body">
                        <label className="control-label">下注賭金(只能依右上角之投注金額下注)</label>
                        <input className="form-control" type="number" value={this.state.getBettingPrice} onChange={() => { }} />
                        <label className="control-label">輸入猜測值</label>
                        <input className="form-control" type="number" onChange={(e) => { this.guess = e.target.value }} />
                    </div>
                    <div className="box-footer">
                        {this.props.ethereumStore.address ? <button type="button" className="btn btn-primary" onClick={this.addguessBtn}>送出</button> : <button className="btn btn-primary" disabled>右上角請先進行登入</button>}
                    </div>
                </div>
                {this.props.ethereumStore.address === this.state.getDeveloperAddress &&
                    [<div key="1" className="box box-primary">
                        <div className="box-header with-border">
                            <h3 className="box-title">開牌與派彩</h3>
                        </div>
                        <div className="box-body">
                            <button type="button" className="btn btn-primary" onClick={this.finishBtn}>送出</button>
                        </div>
                        <div className="box-footer">

                        </div>
                    </div>,

                    <div key="2" className="box box-primary">
                        <div className="box-header with-border">
                            <h3 className="box-title">設置</h3>
                        </div>
                        <div className="box-body">
                            <div className="form-group">
                                <div>
                                    <label className="control-label">設定競爭條件 競爭人數</label>
                                    <input className="form-control" type="number" onChange={(e) => this.contenders = e.target.value} />
                                    <label className="control-label">投注價格(eth)</label>
                                    <input className="form-control" type="number" onChange={(e) => this.bettingprice = e.target.value} />
                                </div>
                            </div>
                        </div>
                        <div className="box-footer">
                            <button className="btn btn-primary" onClick={this.setBettingConditionBtn}>設定</button>
                        </div>
                    </div>]

                }
                {/*<divr" style={{ display: "none" }}>
                    <div>下注賭金<inputr" />(請依右上角之投注金額下注)</div>
                    <div>輸入猜測值
                        <inpute" />
                        <button">下注</button>交易編號:<span">
                        </span>
                    </div>
                </div>
                <divr" style={{ display: "none" }}>
                    <div>輸入一個目標值找最接近的贏家
                        <inpute" />
                        <button">尋找</button>
                        回傳:
                        <span</span>
                    </div>
                    <br />
                    <div>設定競爭條件 競爭人數:
                        <inputs" /> 投注價格(eth):
                        <inpute" />
                        <button">設定</button>
                        交易編號:
                        <span</span>
                    </div>
                    <br />
                    <div>開牌與派彩
                        <button">點擊</button>
                        回傳:
                        <span</span>
                    </div>
                    <br />
                </div> */}
                <div key="1" className="box box-primary">
                    <div className="box-header with-border">
                        <h3 className="box-title">歷史紀錄</h3>
                    </div>
                    <div className="box-body">
                        <button className="btn btn-primary" onClick={() => {
                            this.state.openHistory || this.setState({ openHistory: true })
                        }}>查詢歷史賭局結果</button>
                        {this.getHistoryReact()}
                    </div>
                </div>
            </div>
        )
    }




}
export { Guess };